@import '../../Public/tools.scss';

.p-nav {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: $spacing_100;
  color: $dark_blue;
  font-weight: bold;
  font-size: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.p-nav--logged_out {
  border-bottom: 1px solid $dark_blue;
}
.p-nav--logged_in {
  border-bottom: 1px solid $black;
}

.p-nav__item {
  height: 100%;
  border-left: 1px solid $dark_blue;
  padding: $spacing_150;
  text-decoration: none !important;
  color: inherit !important;
  float: left;
}
.p-nav--logged_in .p-nav__item {
  border-left: 1px solid $black;
  color: $black;
}



