@import '../../Public/tools.scss';

$login_container_width: 500px;

.p-login {
  width: 100%;
  height: 100vh;
}
.p-login__container {
  width: $login_container_width;
  float: left;
  height: 100%;
  background: $white;
  padding: $spacing_200;
  -webkit-box-shadow: 10px 0 0 rgba(17,17,31,.25);
          box-shadow: 10px 0 0 rgba(17,17,31,.25);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.p-login__extra_links {
   margin-top: $spacing_100;
   & a {
     color: $black;
     text-decoration: none;
   }
}
.p-login__art {
  float: right;
  background: url('../../Public/Images/wallet.svg');
  background-size: 15%;
  background-color: #1E3743;
  height: 100%;
  width: calc(100% - #{$login_container_width});
}



