@import '../../Public/tools.scss';

.c-progress {
  width: 100%;
  height: 200px;
  position: relative;
  background: $dark_gray;
  border-bottom: 1px solid $black;
}
.c-progress__so_far {
  height: 100%;
  background: $light_gray;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  width: 0px;
}
.c-progress__left_wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.c-progress__left {
  width: 100%;
}
.c-progress__left h1 {
  margin: 0;
  font-size: 50px;
  padding: 0;
  margin-bottom: $spacing_50;
}
.c-progress__left h2 {
  margin: 0;
  font-size: 30px;
  padding: 0;
}



